import {
  Box,
  Button,
  Card,
  CardContent,
  ThemeProvider,
  createTheme,
} from "@material-ui/core";

import { MainContent } from "./MainContent";
import React from "react";
import { CongratulationsPage } from "../CongratulationsPage";
import KSActionService from "../../../services/KSActionService";

const textTheme = createTheme({
  typography: {
    fontFamily: "Inter",
  },
});

const buttonText = "I got it";

export const InstructionLayout = ({ os }) => {
  const [showCongratPage, setShowCongratPage] = React.useState(false);

  function handleContinue() {
    setShowCongratPage(true);
    KSActionService.clickIGotItBtn();
  }

  return !showCongratPage ? (
    <Box
      style={{
        boxSizing: "border-box",
        marginTop: "0px",
      }}
      elevation={0}
    >
      <Card elevation={0}>
        <CardContent
          style={{
            padding: "8px",
            boxSizing: "border-box",
          }}
        >
          <ThemeProvider theme={textTheme}>
            <MainContent os={os} />
          </ThemeProvider>
        </CardContent>
      </Card>

      <Box style={{ display: "flex", justifyContent: "flex-end" }}>
        <ThemeProvider theme={textTheme}>
          <Button
            style={{
              marginTop: 20,
              marginRight: 15,
              textTransform: "none",
              color: "white",
              width: 150,
              height: 28,
              fontWeight: 600,
            }}
            variant="contained"
            size="small"
            color="primary"
            disableElevation
            onClick={handleContinue}
          >
            {buttonText}
          </Button>
        </ThemeProvider>
      </Box>
    </Box>
  ) : (
    <CongratulationsPage />
  );
};
