import * as React from "react";
import { Box, Step, StepButton, Stepper, makeStyles } from "@material-ui/core";
import { Context } from "./Context";
import { STEPS_ON_LINUX, STEPS_ON_MACOS, STEPS_ON_WINDOWS } from "./constants";

const styles = makeStyles({
  icon: {
    "& .MuiStepIcon-active": { color: "black" },
    "& .MuiStepIcon-completed": { color: "green" },
  },

  description: {
    fontWeight: "medium",
  },
});

export const StepList = () => {
  const [activeStep, setActiveStep] = React.useState(0);
  const [completed, setCompleted] = React.useState({});
  const { setStepIndex } = React.useContext(Context);
  const { localOS } = React.useContext(Context);

  let stepList = null;
  if (localOS === "win32") {
    stepList = STEPS_ON_WINDOWS;
  } else if (localOS === "macosx") {
    stepList = STEPS_ON_MACOS;
  } else if (localOS === "linux") {
    stepList = STEPS_ON_LINUX;
  }

  const handleNext = (step) => {
    setActiveStep(step);
  };

  const handleStep = (step) => () => {
    if (!(step in completed) || completed[step] === false) {
      handleComplete(step);
    } else {
      setNewCompleted(step, false);
      handleComplete(step);
    }

    setStepIndex(step);
  };

  const setNewCompleted = (aStep, state) => {
    const newCompleted = completed;
    newCompleted[aStep] = state;
    setCompleted(newCompleted);
  };

  const handleComplete = (step) => {
    setNewCompleted(activeStep, true);
    handleNext(step);
  };

  const classes = styles();

  return (
    <Box>
      <Stepper
        className={classes.icon}
        nonLinear
        activeStep={activeStep}
        orientation="vertical"
        style={{ padding: "0px", marginTop: "15px" }}
      >
        {stepList.map((label, index) => (
          <Step
            className={classes.description}
            key={label}
            completed={completed[index]}
          >
            <StepButton
              style={{ fontSize: "0.875rem" }}
              color="inherit"
              onClick={handleStep(index)}
            >
              {label}
            </StepButton>
          </Step>
        ))}
      </Stepper>
    </Box>
  );
};
