import { Box, Button, Grid, Typography, makeStyles } from "@material-ui/core";
import { ArrowForward } from "@material-ui/icons";
import {
  CONGRAT_PAGE_TEXT_1,
  CONGRAT_PAGE_TEXT_2,
  CONGRAT_PAGE_TEXT_3,
  ImageOnCongratPage,
} from "./components/constants";
import KSActionService from "../../services/KSActionService";

const buttonText = "Learn more";

export const CongratulationsPage = () => {
  function navigateToDocument() {
    KSActionService.learnMoreAboutCICD();
  }

  return (
    <Grid container spacing={2} style={{ backgroundColor: "#ECF4F2" }}>
      <Grid item xs={4}>
        <Box
          style={{
            position: "relative",
            top: "70px",
            left: "50px",
            bottom: "130px",
            alignItems: "center",
          }}
        >
          <Typography
            style={{ fontFamily: "Inter", fontWeight: 600, fontSize: "20px" }}
          >
            {CONGRAT_PAGE_TEXT_1}
          </Typography>
          <Typography
            style={{ fontFamily: "Inter", marginTop: "8px", fontSize: "14px" }}
          >
            {CONGRAT_PAGE_TEXT_2}
          </Typography>

          <Button
            style={{
              textTransform: "none",
              color: "white",
              backgroundColor: "#000000",
              borderRadius: "4px",
              fontFamily: "Inter",
              fontSize: "12px",
              width: "120px",
              height: "28px",
              marginTop: "20px",
            }}
            variant="contained"
            size="small"
            color="primary"
            disableElevation
            onClick={navigateToDocument}
          >
            {buttonText}{" "}
            <ArrowForward style={{ fontSize: "1.1rem" }}></ArrowForward>
          </Button>
        </Box>
      </Grid>
      <Grid item xs={8} style={{ padding: "0px" }}>
        <Box
          style={{
            display: "flex",
            justifyContent: "flex-end",
          }}
        >
          <img
            src={ImageOnCongratPage}
            alt="Congratulations!"
            style={{
              objectFit: "none",
              width: "536px",
              display: "block",
            }}
          />
        </Box>
      </Grid>
    </Grid>
  );
};
