import { useState } from "react";
import { Context } from "./Context";
import { Box, Divider } from "@material-ui/core";

import { StepList } from "./StepList";
import { StepDescription } from "./StepDescription";

export const MainContent = ({ os }) => {
  const [stepIndex, setStepIndex] = useState(0);
  const localOS = os;

  return (
    <Context.Provider value={{ stepIndex, setStepIndex, localOS }}>
      <Box
        style={{
          display: "flex",
          width: "100%",
          height: "100%",
          justifyContent: "center",
        }}
      >
        <StepList os={os} />
        <Divider
          flexItem
          orientation="vertical"
          style={{
            height: "294px",
            alignSelf: "center",
            marginLeft: "22px",
            marginRight: "32px",
          }}
        />
        <StepDescription os={os} />
      </Box>
    </Context.Provider>
  );
};
