import { Box, Grid, Typography } from "@material-ui/core";
import { AccessTime } from "@material-ui/icons";

import {
  ESTIMATE_TIME_FOR_LINUX,
  ESTIMATE_TIME_FOR_WINDOWS_AND_MACOS,
  INSTRUCTION_TEXT_ON_LINUX,
  INSTRUCTION_TEXT_ON_MACOS,
  INSTRUCTION_TEXT_ON_WINDOWS,
} from "./constants";
import React from "react";
import { Context } from "./Context";

export const DescriptionText = () => {
  const { stepIndex } = React.useContext(Context);
  const { localOS } = React.useContext(Context);

  let instructions = null;
  let estimate_time = null;
  if (localOS === "win32") {
    instructions = INSTRUCTION_TEXT_ON_WINDOWS;
    estimate_time = ESTIMATE_TIME_FOR_WINDOWS_AND_MACOS;
  } else if (localOS === "macosx") {
    instructions = INSTRUCTION_TEXT_ON_MACOS;
    estimate_time = ESTIMATE_TIME_FOR_WINDOWS_AND_MACOS;
  } else if (localOS === "linux") {
    instructions = INSTRUCTION_TEXT_ON_LINUX;
    estimate_time = ESTIMATE_TIME_FOR_LINUX;
  }

  return (
    <Box
      style={{
        display: "flex",
        flexDirection: "column",
        padding: "16px",
        width: "50%",
      }}
    >
      <Box>
        <Typography
          style={{
            fontWeight: 400,
            fontSize: "0.875rem",
          }}
          dangerouslySetInnerHTML={{
            __html: instructions[stepIndex],
          }}
        ></Typography>
      </Box>
      <Box style={{ display: "flex", alignItems: "center", marginTop: "5px" }}>
        <AccessTime style={{ fontSize: "0.875rem", marginRight: "10px" }} />
        <Typography
          style={{ fontSize: "0.875rem", marginRight: "10px" }}
          component="p"
        >
          ETA
        </Typography>
        <div
          style={{
            borderRight: "1px solid gray",
            height: "14px",
            marginRight: "10px",
          }}
        />
        <Typography style={{ fontSize: "0.875rem", marginRight: "10px" }}>
          {estimate_time[stepIndex]}m
        </Typography>
      </Box>
    </Box>
  );
};
