import { Box } from "@material-ui/core";
import { DescriptionImage } from "./DescriptionImage";
import { DescriptionText } from "./DescriptionText";
import { INSTRUCTION_TEXT_BACKGROUND_COLOR } from "./constants";
import React from "react";
import { Context } from "./Context";

export const StepDescription = () => {
  const { stepIndex } = React.useContext(Context);

  return (
    <Box
      style={{
        display: "flex",
        backgroundColor: INSTRUCTION_TEXT_BACKGROUND_COLOR[stepIndex],
        width: 623,
        height: 294,
        borderRadius: "4px",
        paddingLeft: "32px",
        paddingTop: "29px",
        boxSizing: "border-box",
      }}
    >
      <DescriptionText />
      <DescriptionImage />
    </Box>
  );
};
