import { Box, makeStyles } from "@material-ui/core";
import { Context } from "./Context";
import React from "react";

import {
  INSTRUCTION_IMAGES_ON_MACOS,
  INSTRUCTION_IMAGES_ON_WINDOWS,
  INSTRUCTION_IMAGES_ON_LINUX
} from "./constants";

const styles = makeStyles((theme) => ({
  image: {
    width: "100%",
    height: "auto",
  },
}));

export const DescriptionImage = () => {
  const classes = styles();
  const { stepIndex } = React.useContext(Context);
  const { localOS } = React.useContext(Context);

  let instructions = null;
  if (localOS === "win32") {
    instructions = INSTRUCTION_IMAGES_ON_WINDOWS;
  } else if (localOS === "macosx") {
    instructions = INSTRUCTION_IMAGES_ON_MACOS;
  } else if (localOS === "linux") {
    instructions = INSTRUCTION_IMAGES_ON_LINUX;
  }

  return (
    <Box
      style={{
        display: "flex",
        alignSelf: "flex-end",
        justifyContent: "flex-end",
      }}
    >
      <img
        src={instructions[stepIndex]}
        alt="instructions"
        className={classes.image}
        style={{
          objectFit: "cover",
          width: "600px",
          height: "213px",
        }}
      />
    </Box>
  );
};
